import axios from 'axios';
import React, {useState,useEffect} from 'react'

//create context
const AppContext = React.createContext();
//1.provider-works as a distributor
//2.consumer//we dont have to use the consumer



const Context = ({children}) => {
  const [trackInfo, settrackInfo] = useState([]);
// const [trackInfo, settrackInfo] = useState({trackList:[],heading:''});
//  {
//    track_list : [
//    {track:{track_name:'abc'}},
//   {track:{track_name:'123'}},
//     ],
//      heading:'Top 10 Tracks'
//    }
// )

useEffect(() => {
  axios.get(`https://cors-access-allow.herokuapp.com/https://api.musixmatch.com/ws/1.1/chart.tracks.get?chart_name=top&page=1&page_size=10&country=in&f_has_lyrics=1&apikey=${process.env.REACT_APP_MM_KEY}`)
  .then(res=>{
    // console.log(res.data);
    settrackInfo(res.data.message.body.track_list);
  })
  .catch(err=>console.log(err))
  // return () => {
   
  // }
}, [])
console.log(trackInfo);
  return (
    <AppContext.Provider value={{trackInfo:trackInfo,settrackInfo:settrackInfo}} >
    {children}
    </AppContext.Provider>
  
  )
}

export  {AppContext,Context}













