import React ,{useEffect, useState} from 'react'
import axios from 'axios';
import { Link, useParams } from 'react-router-dom';
import Spinner from '../layout/Spinner';
const Lyrics = () => {
  const [lyrics, setlyrics] = useState();
  const [track, settrack] = useState('');
  const {id} = useParams();
  //console.log(useParams());
  useEffect(() => {
    axios.get(`https://cors-access-allow.herokuapp.com/https://api.musixmatch.com/ws/1.1/track.lyrics.get?track_id=${id}&apikey=${process.env.REACT_APP_MM_KEY}`)
  .then(res=>{
    //console.log(res.data);
    setlyrics(res.data.message.body.lyrics);
    
    axios.get(`https://cors-access-allow.herokuapp.com/https://api.musixmatch.com/ws/1.1/track.get?track_id=${id}&apikey=${process.env.REACT_APP_MM_KEY}`)
    .then(res=>{
      console.log(res.data);
      settrack(res.data.message.body.track);
    }).catch(err=>console.log(err))

  })
  .catch(err=>console.log(err))
  

},[]);
 console.log(track);
// console.log(track.primary_genres.music_genre_list[0]);
// console.log(track.primary_genres.music_genre_list[0].music_genre.music_genre_name);
  return (
    <div style={{display:'flex', flexDirection:'column',alignItems:'center'}}>
      {track ? <>
      <Link to='/' style={{alignSelf:'flex-start'}}  className='btn btn-dark btn-sm mb-4'>Go Back</Link>
      <div className="card">
        <h5 className="card-header">
          {track.track_name} by <span className='text-secondary'>{track.artist_name}</span>
        </h5>
        <div className="card-body">
          <p className="card-text">{lyrics.lyrics_body}</p>
        </div>
      </div>
      <ul className="list-group mt-3" style={{alignSelf:'flex-start'}}>
        <li className="list-group-item">
          <strong>Album ID:</strong>:{track.album_id}
        </li>
        <li className="list-group-item">
         {track && <div>
          <strong>Song Genre: </strong>
          {track.primary_genres.music_genre_list[0] ?
            track.primary_genres.music_genre_list[0].music_genre.music_genre_name
            : 'NA'
          }
         </div>
          }
        </li>
        <li className="list-group-item">
          <strong>Explicit Words:</strong>{track.explicit === 0 ? 'NO' :'YES'}
        </li>
        {/* <li className="list-group-item">
          <strong>Release Date:</strong>{track.first_release_date}
        </li> */}
      </ul>
      </>
      :<Spinner style={{}}></Spinner>}
    </div>
  )
}

export default Lyrics
