import axios from 'axios';
import React,{useContext, useState} from 'react'
import {AppContext} from '../../Context';
const Search = () => {
  const [trackTitle, settrackTitle] = useState("");
  const {trackInfo,settrackInfo} = useContext(AppContext);
  // const onChangeSearch=(e)=>{
  //   settrackTitle(e.target.value);
  // }
  const findTrack=(e)=>{
    e.preventDefault();
    axios.get(`https://cors-access-allow.herokuapp.com/https://api.musixmatch.com/ws/1.1/track.search?q_track=${trackTitle}&page_size=10&page=1&s_track_rating=desc&apikey=${process.env.REACT_APP_MM_KEY}`)
    .then(res=>{
    // console.log(res.data);
    settrackInfo(res.data.message.body.track_list);
    settrackTitle('');
    // settrackTitle(res.data.message.body.track_list);
    })
    .catch(err=>console.log(err))
  // return () => {
   
  // }
};
 
  return (
    <div>
      <div className="card card-body mb-4 p-4">
        <h1 className="display-4 text-center">
          <i className="fas fa-music">Search For A Music</i>
        </h1>
        <p className="lead text-center">
          Get the lyrics for any song
        </p>
        <form action="" >
          <div className="form-group">
            <input type="text" className="form-control form-control-lg" placeholder='Song title...'
            name='trackTitle' value={trackTitle}
            onChange={(e)=>{
              settrackTitle(e.target.value);
            }}/>
          </div>
          <button className="btn btn-primary btn-lg btn-block mb-5 mt-3" style={{width:'100%'}}
          type="submit" onClick={findTrack}>
            Get Track Lyrics
          </button>
        </form>
      </div>
    </div>
  )
}

export default Search
