import React, {useContext } from 'react'
import {AppContext} from '../../Context';
import Spinner from '../layout/Spinner';
import Track from './Track'
const Tracks = () => {
  const {trackInfo,settrackInfo} = useContext(AppContext);
  // console.log(trackInfo);
  
  const {heading} = trackInfo;
  console.log(trackInfo);
  return (
    
    <div>
     {trackInfo ? 
     <>
     <h3 className="text-center mb-4">{heading}</h3>
      <div className='row'>
        {
          trackInfo.map((item)=>(
            // console.log(item.track.track_id);
            <Track key={item.track.track_id} track ={item.track} ></Track>
          ))
        }
        </div>
     </>
     : <div style={{display:'flex',justifyContent:'center',width:'100vh'}}>
       <Spinner></Spinner>
     </div>
     
     }
      
    </div>
  )
}

export default Tracks

